'use client'

import { useState, useMemo } from "react";
import Head from "next/head";
import { Product } from "./lib/types";
import { generateProducts } from "./lib/products";

import Header from "./components/Header";
import Filters from "./components/Filters";
import ProductGrid from "./components/ProductGrid";
import ProductModal from "./components/ProductModal";

export default function ShopPage() {
  const [products, setProducts] = useState<Product[]>(generateProducts());
  const [query, setQuery] = useState("");
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(2000);
  const [sort, setSort] = useState("price-asc");
  const [perPage, setPerPage] = useState(8);
  const [page, setPage] = useState(1);
  const [modalProduct, setModalProduct] = useState<Product | null>(null);

  const filteredProducts = useMemo(() => {
    let arr = products.filter(
      (p) =>
        p.price >= minPrice &&
        p.price <= maxPrice &&
        (p.sku.toLowerCase().includes(query.toLowerCase()) ||
          p.title.toLowerCase().includes(query.toLowerCase()) ||
          p.artist.toLowerCase().includes(query.toLowerCase()))
    );

    switch (sort) {
      case "price-asc":
        arr.sort((a, b) => a.price - b.price);
        break;
      case "price-desc":
        arr.sort((a, b) => b.price - a.price);
        break;
      case "sales-desc":
        arr.sort((a, b) => b.sales - a.sales);
        break;
      case "release-desc":
        arr.sort((a, b) => b.release - a.release);
        break;
    }

    return arr;
  }, [products, query, minPrice, maxPrice, sort]);

  const pagedProducts = useMemo(() => {
    const start = (page - 1) * perPage;
    return filteredProducts.slice(start, start + perPage);
  }, [filteredProducts, page, perPage]);

  const toggleFav = (id: number) => {
    setProducts((prev) =>
      prev.map((p) => (p.id === id ? { ...p, fav: !p.fav } : p))
    );
  };

  return (
    <>
      <Head>
        <title>ALeToys — Shop</title>
      </Head>
      <Header onSearchChange={setQuery} />
      <main style={{ display: "flex" }}>
        <Filters
          minPrice={minPrice}
          maxPrice={maxPrice}
          sort={sort}
          perPage={perPage}
          onMinPriceChange={setMinPrice}
          onMaxPriceChange={setMaxPrice}
          onSortChange={setSort}
          onPerPageChange={setPerPage}
        />
        <div style={{ flex: 1, padding: "1rem" }}>
          <ProductGrid
            products={pagedProducts}
            onToggleFav={toggleFav}
            onOpenModal={setModalProduct}
          />
        </div>
      </main>
      <ProductModal product={modalProduct} onClose={() => setModalProduct(null)} />
    </>
  );
}