// lib/products.ts
import productsData from '../data/products.json';

export interface Product {
  id: string;
  title: string;
  price: number;
  image: string;
}

export function getAllProducts(): Product[] {
  // productsData 是静态 JSON 导入（位于 data/products.json）
  return (productsData as Product[]).slice();
}