
import styles from "../shop.module.css";

interface FiltersProps {
  minPrice: number;
  maxPrice: number;
  sort: string;
  perPage: number;
  onMinPriceChange: (v: number) => void;
  onMaxPriceChange: (v: number) => void;
  onSortChange: (v: string) => void;
  onPerPageChange: (v: number) => void;
}

export default function Filters({
  minPrice,
  maxPrice,
  sort,
  perPage,
  onMinPriceChange,
  onMaxPriceChange,
  onSortChange,
  onPerPageChange,
}: FiltersProps) {
  return (
    <aside className={styles.filters}>
      <h2>Filters</h2>
      <div className={styles.filterGroup}>
        <label>Price Min</label>
        <input
          type="number"
          value={minPrice}
          onChange={(e) => onMinPriceChange(Number(e.target.value) || 0)}
        />
        <label>Price Max</label>
        <input
          type="number"
          value={maxPrice}
          onChange={(e) => onMaxPriceChange(Number(e.target.value) || 2000)}
        />
      </div>

      <div className={styles.filterGroup}>
        <label>Sort</label>
        <select value={sort} onChange={(e) => onSortChange(e.target.value)}>
          <option value="price-asc">Price Low → High</option>
          <option value="price-desc">Price High → Low</option>
          <option value="sales-desc">Top Sales</option>
          <option value="release-desc">Newest</option>
        </select>
      </div>

      <div className={styles.filterGroup}>
        <label>Items per Page</label>
        <select
          value={perPage}
          onChange={(e) => onPerPageChange(Number(e.target.value))}
        >
          <option value={8}>8</option>
          <option value={12}>12</option>
          <option value={16}>16</option>
        </select>
      </div>
    </aside>
  );
}